 import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import html2canvas from "html2canvas";
import jsPDF from "jspdf";
import "../styles/PageLayout.css";
import "../styles/Animations.css";

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

const ChartPage = () => {
  const [chartData, setChartData] = useState(null);

  useEffect(() => {
    fetch("/api/names/chart-data")
      .then((res) => res.json())
      .then((data) => {
        const labels = data.labels;
        const datasets = data.datasets.map((ds, index) => ({
          ...ds,
          borderWidth: 3,
          pointRadius: 5,
          pointHoverRadius: 7,
        }));

        setChartData({
          labels,
          datasets,
        });
      });
  }, []);

  const handleExport = (format) => {
    const chartContainer = document.getElementById("chart-section");

    html2canvas(chartContainer).then((canvas) => {
      const imgData = canvas.toDataURL("image/png");

      if (format === "png") {
        const link = document.createElement("a");
        link.download = "baby_names_chart.png";
        link.href = imgData;
        link.click();
      } else if (format === "pdf") {
        const pdf = new jsPDF("landscape");
        const width = pdf.internal.pageSize.getWidth();
        const height = (canvas.height * width) / canvas.width;
        pdf.addImage(imgData, "PNG", 0, 10, width, height);
        pdf.save("baby_names_chart.pdf");
      }
    });
  };

  return (
    <div className="page-container">
      <h1 className="page-header">Baby Names Popularity Trends</h1>

      <div className="page-section" id="chart-section" style={{ minHeight: "65vh" }}>
        {chartData ? (
          <Line
            data={chartData}
            options={{
              responsive: true,
              maintainAspectRatio: false,
              plugins: {
                legend: {
                  position: "top",
                  labels: {
                    font: {
                      size: 16,
                    },
                  },
                },
                title: {
                  display: true,
                  text: "Popularity of Baby Names Over Years",
                  font: {
                    size: 22,
                    weight: "bold",
                  },
                },
                tooltip: {
                  bodyFont: {
                    size: 14,
                  },
                  titleFont: {
                    size: 16,
                  },
                },
              },
              scales: {
                x: {
                  ticks: {
                    font: {
                      size: 14,
                    },
                  },
                  title: {
                    display: true,
                    text: "Year",
                    font: {
                      size: 18,
                    },
                  },
                },
                y: {
                  ticks: {
                    font: {
                      size: 14,
                    },
                  },
                  title: {
                    display: true,
                    text: "Count",
                    font: {
                      size: 18,
                    },
                  },
                },
              },
            }}
          />
        ) : (
          <p style={{ fontSize: "18px", textAlign: "center" }}>Loading chart data...</p>
        )}
      </div>

      <div className="page-section" style={{ textAlign: "center" }}>
        <button onClick={() => handleExport("png")} className="export-btn">Export as PNG</button>
        <button onClick={() => handleExport("pdf")} className="export-btn" style={{ marginLeft: "1rem" }}>Export as PDF</button>
      </div>
    </div>
  );
};

export default ChartPage;
